export default {
  props: ['id', 'title', 'client', 'fav', 'noContent'],
  data() {
    return {
      card: {
        name: this.title,
        client: this.client,
        favorite: this.favorite,
        id: this.id
      }
    }
  },
  methods: {
    addToFav (opt) {
      if (opt)
        this.card.favorite = "X"
      else
        this.card.favorite = ""

      this.$parent.addToFav(this.card);
    },

    remove() {
      this.$parent.remove(this.card);
    }
  }
}
